/* root */
@use "fonts";

:root {
	--cream: hsl(30, 38%, 92%);
	--dark-cyan: hsl(158, 36%, 37%);
	--dark-grayish-blue: hsl(228, 12%, 48%);
	--very-dark-blue: hsl(212, 21%, 14%);
	--white: hsl(0, 0%, 100%);
	--font-family-fraunces: "Fraunces", serif;
	--font-family-montserrat: "Montserrat", sans-serif;
}
